<template><section><html><head></head><body><h1>淹没分析</h1>
<p><code v-pre="">vc-analytics-flood</code> 组件用于模拟淹没分析。其实质是用 <code v-pre="">vc-primitive-classification</code> 加载 <code v-pre="">vc-geometry-polygon</code>，通过动态修改 <code v-pre="">vc-geometry-polygon</code> 的 <code v-pre="">extrudedHeight</code> 属性拉伸成一个闭合体对象，从而模拟淹没分析。<strong>注意</strong>需要场景加载地形或 3DTiles 才可以分析。</p>
<h2>示例</h2>
<h3>淹没分析</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-analytics-flood ref="flood" v-if="flag" :min-height="minHeight" :max-height="maxHeight" :speed="speed" :polygon-hierarchy="polygonHierarchy" @activeevt="activeEvt">
        </vc-analytics-flood>
        <vc-provider-terrain-cesium></vc-provider-terrain-cesium>
        <vc-layer-imagery>
          <vc-provider-imagery-bingmaps :url="url" :bm-key="bmKey" map-style="Aerial"></vc-provider-imagery-bingmaps>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
         <md-input-container>
          <label>minHeight</label>
          <md-input v-model.number="minHeight"></md-input>
        </md-input-container>
        <md-input-container>
          <label>maxHeight</label>
          <md-input v-model.number="maxHeight"></md-input>
        </md-input-container>
        <span>speed</span>
        <vue-slider v-model="speed" :min="1" :max="100" :interval="1"></vue-slider>
        <md-button class="md-raised md-accent" @click="toggle">{{ flooding ? 'Stop' : 'Start' }}</md-button>
        <md-button class="md-raised md-accent" @click="clear">Clear</md-button>
      </div>
    </div>
  </template>
  
</doc-preview>
