          viewer.<span class="hljs-property">scene</span>.<span class="hljs-property">globe</span>.<span class="hljs-property">depthTestAgainstTerrain</span> = <span class="hljs-literal">true</span>
          viewer.<span class="hljs-property">camera</span>.<span class="hljs-title function_">setView</span>({
            <span class="hljs-attr">destination</span>: <span class="hljs-keyword">new</span> <span class="hljs-title class_">Cesium</span>.<span class="hljs-title class_">Cartesian3</span>(-<span class="hljs-number">1432246.8223880068</span>, <span class="hljs-number">5761224.588247942</span>, <span class="hljs-number">3297281.1889481535</span>),
