<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>minHeight</td>
<td>Number</td>
<td>0</td>
<td><code v-pre="">optional</code> 最小高程。</td>
</tr>
<tr>
<td>maxHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre="">require</code> 最大高程。</td>
</tr>
<tr>
<td>speed</td>
<td>Number</td>
<td>10</td>
<td><code v-pre="">optional</code> 速度。</td>
</tr>
<tr>
<td>polygonHierarchy</td>
<td>Array</td>
<td></td>
<td><code v-pre="">require</code> 指定构建淹没分析多边形的经纬度数组。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
<tr>
<td>activeEvt</td>
<td>{isActive: Boolean}</td>
<td>淹没分析组件中 <code v-pre="">flooding</code> 状态改变时触发，返回淹没分析是否开始。</td>
</tr>
</tbody>
</table>
</body></html></section></template>

<script>
    export default {
      data () {
        return {
          minHeight: 0,
          maxHeight: 7000,
          speed: 10,
          polygonHierarchy: [
            {lng: 102.1, lat: 29.5},
            {lng: 106.2, lat: 29.5},
            {lng: 106.2, lat: 33.5},
            {lng: 102.1, lat: 33.5}
          ],
          url: 'https://dev.virtualearth.net',
          bmKey: 'AgcbDCAOb9zMfquaT4Z-MdHX4AsHUNvs7xgdHefEA5myMHxZk87NTNgdLbG90IE-', // 可到(https://www.bingmapsportal.com/)申请Key。
          flooding: false,
          flag: true
        }
      },
      methods: {
        ready (cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          window.vm = this
          const {Cesium, viewer} = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.scene.debugShowFramesPerSecond = true
          viewer.camera.setView({
            destination: new Cesium.Cartesian3(-1432246.8223880068, 5761224.588247942, 3297281.1889481535),
            orientation: {
              heading: 6.20312220367255,
              pitch: -0.9937536846355606,
              roll: 0.002443376981836387
            }
          })
        },
        toggle (){
          this.$refs.flood.flooding = !this.$refs.flood.flooding
        },
        activeEvt (_) {
          console.log('ad')
          this.flooding = _.isActive
        },
        clear () {
          this.$refs.flood.clear()
        }
      }
    }
  </script>